import * as React from "react";
import { RouteComponentProps } from "react-router";
import Navigation from "src/components/Navigation/Navigation";
import styled from "styled-components";
import Banner from "src/components/Banner";

export interface VisionProps extends RouteComponentProps {}

const Content = styled.div`
    width: 100%;
    padding-bottom: 200px;
    font-size: 14px;
`;

const Desc = styled.div`
    padding: 0 15%;
    & > div {
        display: flex;
        margin-bottom: 42px;
        & > div {
            width: calc(20% - 45px);
            margin-right: 45px;
            font-size: 18px;
        }
        & > p {
            width: 80%;
            line-height: 1.71;
        }
    }
`;

export default class Vision extends React.Component<VisionProps, any> {
    public render() {
        return (
            <>
                <Navigation list={[{ name: "홈", link: "/" }, { name: "유니온", link: "/union/" }, { name: "비전", link: "/vision/" }]} />
                <Content>
                    <Banner>
                        <img src={require("../../assets/union/vision-banner.png")} alt="" />
                    </Banner>
                    <Desc>
                        <div>
                            <div />
                            <p style={{ fontSize: 32, fontWeight: "bold" }}>우리는 사람을 향하는 열린 공간을 만듭니다.</p>
                        </div>
                        <div>
                            <div />
                            <p>
                                주식회사 유니언플레이스는 <br /> 노후 건물에 주거∙업무∙숙박 등의 콘텐츠를 넣어 비즈니스 모델을 구축하는{" "}
                                <br />
                                '공간브랜드기획사'이자 '공간매니지먼트사'입니다.
                                <br />
                                젊고 창의적인 기획이 담긴 도시 재생 프로젝트를 통해 <br /> 활력을 잃은 로컬을 살리는 것은 물론 '사람 중심'의
                                새로운 문화를 만들어 갑니다.
                            </p>
                        </div>
                        <div>
                            <div />
                            <p>
                                <img src={require("../../assets/union/vision01.png")} style={{ width: "50%" }} />
                            </p>
                        </div>
                        <div>
                            <div />
                            <p>
                                <h2 lang="en" style={{ fontSize: 34, marginBottom: 42 }}>
                                    Live / Work / Stay / Play Space
                                </h2>
                                <p style={{ marginBottom: 42 }}>
                                    라이프스타일의 재탄생! <br />
                                    유니언플레이스가 도시재생을 통한 공간의 새로운 패러다임을 만들어 갑니다.
                                </p>
                                <h3 lang="en">#LIVE PROJECT</h3>
                                <p style={{ marginBottom: 42 }}>
                                    높은 보증금과 치솟는 월세, 꿈 많은 청년들은 점점 좁은 공간에 갇히고 있습니다. <br /> 유니언플래이스는
                                    '청년 주거 환경 개선 사업'을 통해 그들의 꿈과 생각을 나누는 공간을 넓혀가고 있습니다.
                                </p>

                                <h3 lang="en">#WORK PROJECT</h3>
                                <p style={{ marginBottom: 42 }}>
                                    청년 취업을 위한 정부의 여러 정책에도 청년 실업률을 해를 거듭할수록 높아지고 있습니다. <br />
                                    공유 업무 공간이 만들어내는 시너지를 통해 많은 청년들이 이 공간에서 자신을 브랜딩하고 꿈을 찾습니다.
                                </p>

                                <h3 lang="en">#STAY PROJECT</h3>
                                <p style={{ marginBottom: 42 }}>
                                    호텔에 'S'(소셜라이징, 셰어링, 스토리)를 '더한' 호스텔을 만들었습니다.
                                    <br /> 국내 최초 소셜라이징 호스텔인 '업플로호스텔'을 런칭해 <br />
                                    거주, 업무, 여가를 한곳에서 즐길 수 있는 코-리빙 스페이스를 구축했습니다.
                                </p>

                                <h3 lang="en">#PLAY PROJECT</h3>
                                <p style={{ marginBottom: 42 }}>
                                    우리는 사람과 사람을 이어주는 데에 ‘즐거움’이 핵심이라고 생각했습니다. <br />
                                    함께 운동을 하고, 같이 영어를 배우고, 게임을 즐기고, 커피와 빵을 마시면서 우리는 즐거워합니다.
                                    <br />
                                    유니언플레이스는 이런 즐거움 속에서 소통과 함께의 가치를 실천해갑니다.
                                </p>
                            </p>
                        </div>
                    </Desc>
                </Content>
            </>
        );
    }
}



